////////* MIXINS */////////
@mixin headings {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @content;
  }
}
@mixin clearfix {
  &:after {
    content: '';
    clear: both;
    display: table;
  }
}
@mixin flex($arg: $mobile) {
  // when to break to one column
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  @if $arg != 'full' {
    @media #{$arg} {
      flex-direction: column;
    }
  }
}
@mixin vert_align {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  //left:$base;
  //right:$base;
}
//COVER
@mixin cover($dir:'center center', $url: '') {
  background-image: url($url);
  background-size: cover;
  background-position: $dir;
}
@mixin spacers {
  position: relative;

  &:not(:last-child) {
    &:after {
      content: '';
      height: 5px;
      width: 5px;
      position: absolute;
      right: 0;
      transform: translateX(50%) translateY(-50%);
      background: currentColor;
      border-radius: 50%;
      top: 50%;
    }
  }
}
// OVERLAY
@mixin overlay($color: #000, $opac: .25) {
  position: relative;

  > .container {
    @extend .bring-forth;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    pointer-events: none;
    left: 0;
    right: 0;
    background: $color;
    opacity: $opac;
    z-index: 99;
    @if $opac < .5 {
      @media #{$tablet} {
        //opacity: $opac + .075;
      }
      @media #{$mobile} {
        //opacity: $opac + .15;
      }
    }
  }
}
@mixin bg_pattern($img: 'data:image/svg+xml;utf8;base64,', $size: 3px) {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: .5;
    background-image: url($img);
    background-position: center center;
    background-size: $size;
    z-index: 99;
    pointer-events: none;
  }
}
// pseudo width
@mixin pseudo_width($color: inherit) {
  position: relative;

  &:after,
  &:before {
    content: '';
    position: absolute;
    top: 0;
    background-position: center center;
    bottom: 0;
    width: 100vw;
    background: $color;
    display: block;
    z-index: 99;
  }

  &:before {
    right: 100%;
  }

  &:after {
    left: 100%;
  }
}
// lines
@mixin bars($color: currentColor) {
  display: inline-block;
  position: relative;

  &:after,
  &:before {
    opacity: .25;
    content: '';
    height: $border-width;
    background: $color;
    position: absolute;
    @include vert_align;
    display: block;
    width: 500%;
  }

  &:before {
    left: 100%;
    margin-left: 1em;
  }

  &:after {
    right: 100%;
    margin-right: 1em;
  }
}
@mixin gradient($color:#000, $dir:right, $opac: .8) {
  &:after {
    content: '';
    position: absolute;
    display: block;
    bottom: -1px;
    left: 0;
    right: 0;
    pointer-events: none;
    height: 100%;
    opacity: $opac;
    z-index: 1;
    background-color: $color;
    background: -webkit-linear-gradient($dir, rgba($color,1) 0%, rgba($color,0) 100%);
    background: -o-linear-gradient($dir, rgba($color,1) 0%, rgba($color,0) 100%);
    background: linear-gradient($dir, rgba($color,1) 0%, rgba($color,0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#000000', GradientType=1);
  }
}
// BUTTONS / ICONS
@mixin link_icon($dir: 'left', $icon: "\f10a") {
  @if $dir == 'left' {
    &:before {
      content: $icon;
      font-family: ionicons;
      margin-right: 7px;
      display: inline-block;
      vertical-align: middle;
      position: relative;
      font-size: inherit;
      bottom: 1px;
    }
  } @else {
    &:after {
      content: $icon;
      font-family: ionicons;
      margin-left: 7px;
      display: inline-block;
      vertical-align: middle;
      position: relative;
      bottom: 1px;
      font-size: inherit;
    }
  }

  &:after,
  &:before {
    color: inherit;
    color: currentColor;
  }
}
// target retina displays
@mixin iOS {
  @media (min--moz-device-pixel-ratio: 1.3), (-o-min-device-pixel-ratio: 2.6/2), (-webkit-min-device-pixel-ratio: 1.3), (min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    @content;
  }

  .safari & {
    @content;
  }
}
////////// ** EXTENDS ** /////////
a[href*='pdf'] {
  @include link_icon('left', "\f170")
}
// for wysiwyg areas
.last-margin {
  > *:last-child {
    margin-bottom: 0;
  }
}

.list-border {
  &:not(:last-of-type) {
    border-bottom: $border-width solid $gray;
  }
}

.list-spacing {
  margin-bottom: 2em;
  padding-bottom: 2em;
}

.text-shadow {
  text-shadow: 0 0 15px rgba(0,0,0,.15);
}

.box-shadow {
  box-shadow: 0 10px 20px rgba(0,0,0,.1);
}
// reversing color to white
html .invert {
  li.menu-item-has-children {
    a:before {
      display: none;
    }

    a:after {
      margin-bottom: $border-width;
    }

    .sub-menu {
      border: none;
    }
  }

  .menu-toggle {
    span {
      background: white;
    }
  }

  .custom-logo-link {
    img {
      //  @extend .invert-image;
    }
  }
  color: white;

  * {
    &:after,
    &:before {
      color: currentColor;
      border-color: currentColor;
    }
  }

  #searchsubmit {
    color: white;
  }

  a,
  a:visited {
    &:not([class*=btn]) {
      color: white;

      &:hover {
        color: $primary;
      }
    }
  }

  .headline-container {
    *:after,
    *:before {
      background: white;
    }
  }

  .carousel {
    @extend .flickity-light;
  }
  // add more shiz here
}

.shine {
  transition: filter .5s;

  &:hover {
    filter:brightness(130%);
  }
}

.bring-forth {
  position: relative;
  z-index: 999;
}

.bg-img {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  object-fit: cover;
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  transform: translate3D(0,0,0) scale(1.2); // make sure this matches parallax js css
  will-change: transform;
  //image-rendering:pixelated;
  pointer-events: none;
}

.erase {
  background: none;
  appearance: none;
  border: none;
  outline: none;
  box-shadow: none;
}
//nth child formula (up to 4)
.child-formula {
  &:only-child {
    width: 100%;
  }

  &:nth-child(4):last-child,
  &:nth-last-child(2):nth-child(2),
  &:nth-last-child(2):nth-child(3),
  &:nth-last-child(3):nth-child(2),
  &:nth-last-child(4):first-child {
    width: 25%;
  }

  &:nth-child(3):last-child,
  &:nth-last-child(2):nth-child(2),
  &:nth-last-child(3):first-child {
    width: 33.3%;
  }

  &:nth-child(2):last-child,
  &:nth-last-child(2):first-child {
    width: 50%;
  }
}

.invert-image {
  filter:brightness(0) invert(1);
}
@mixin equal_margin($val) {
  > * {
    margin-bottom: $val;
  }
}